.form-group {
    display: block;
    margin-left: 3cm;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0.25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    width: 10cm;
    margin: 1;
}
.Add{
    margin-left:3cm ;
    margin-top: 1cm;
    background-color:rgb(236, 140, 51);
    display: inline-block;
    border-radius: 1rem;
    width:4cm ;
    height: 1cm;
    margin-bottom: 1cm;
    outline:rgb(236, 140, 51)  ;
    outline-width: 100mm;
    font: bold;
    font-stretch: narrower;
}


